
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
.brand {
  position: relative;
  background-color: #ededed;
}
.brand .nav-head {
    height: 50px;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    position: fixed;
    background-color: white;
    position: relative;
}
.brand .nav-head:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.brand .nav-head i {
      display: inline-block;
      width: 78px;
      height: 20px;
      background-size: 78px 20px;
      background-image: url("./images/tuzi_word@2x.png");
      margin: 15px auto;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.brand .nav-head i {
          background-image: url("./images/tuzi_word@3x.png");
}
}
.brand .nav-head a {
      display: inline-block;
      width: 21px;
      height: 21px;
      background-size: 21px 21px;
      background-image: url("./images/search@2x.png");
      margin: 15px;
      position: absolute;
      right: 10px;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.brand .nav-head a {
          background-image: url("./images/search@3x.png");
}
}



